import { INCREMENT, DECREMENT } from "./types";

//// reducer for Likes component //////

const initialState = {
  likes: 0,
}
export const likesReducer = (state = initialState, action) => {
  switch (action.type) {
    case INCREMENT:
      return {
        ...state,
        likes: state.likes + 1,
      }
    case DECREMENT:
      if (state.likes === 0) {
        return state;
      }
      return {
        ...state,
        likes: state.likes - 1,
      }
    default:
      return state;
  }
}